<template>
	<div>
		<title-bar title="我的收款码" backBtn="true"></title-bar>
		<div class="content-box-title-bottom">
			<div class="mdui-tab" mdui-tab>
			  <a href="#tab1" class="mdui-ripple">微信</a>
			  <a href="#tab2" class="mdui-ripple">支付宝</a>
			</div>
			<div class="tab-content">
				<div id="tab1" class="mdui-p-a-2">
					<img :src="userInfo.wechatQrCode" @click="putQrCode(1)"/>
				</div>
				<div id="tab2" class="mdui-p-a-2">
					<img :src="userInfo.alipayQrCode" @click="putQrCode(2)"/>
				</div>
				<div style="display: none;">
					<input type="file" ref="fileInput" accept="image/*" @change="doUpload"/>
				</div>
			</div>
		</div>
		<api-loading :class="{'loading-open': loadingShow}"></api-loading>
	</div>
</template>

<script>
	import titleBar from './titleBar.vue'
	import apiLoading from './apiLoading.vue'
	
	export default {
	  name: 'persionQrcode',
	  components:{
		  titleBar,
		  apiLoading,
	  },
	  data(){
		  return {
			  userInfo: {},
			  selectType: 1,
			  loadingShow: false
		  }
	  },
	  methods:{
		  putQrCode(selectType){
			  this.$data.selectType = selectType
			  return this.$refs.fileInput.click();
		  }, doUpload(){
			  let file = this.$refs.fileInput.files[0];
			  this.$refs.fileInput.value = ''
			  let formData = new FormData();
			  formData.append("file", file)
			  var options = {  // 设置axios的参数
				   headers: { 
					'Content-Type': 'multipart/form-data'
				   }
			  }
			  this.$data.loadingShow = true
			  this.ser.post("comm/upload", formData, options).then(res => {
				  if(this.$data.selectType == 1){
					  // 微信
					  this.$data.userInfo.wechatQrCode = this.ser.ctx + "/comm/display/" + res.data.responseBody.url
					  this.ser.put("api/v1/mobile/user/wechat-code", {
						  wechatQrCode: "/comm/display/" + res.data.responseBody.url
					  }).then(res => {
						  mdui.snackbar("上传成功")
					  }).finally(()=>{
						  this.$data.loadingShow = false
					  })
				  }else{
					  // 支付宝
					  this.$data.userInfo.alipayQrCode = this.ser.ctx + "/comm/display/" + res.data.responseBody.url
					  this.ser.put("api/v1/mobile/user/alipay-code", {
						  alipayQrCode: "/comm/display/" + res.data.responseBody.url
					  }).then(res => {
						  mdui.snackbar("上传成功")
					  }).finally(()=>{
						  this.$data.loadingShow = false
					  })
				  }
			  }).catch(err => {
				  this.$data.loadingShow = false
			  })
		  }
	  },
		created(){
			this.ser.get("api/v1/mobile/user/info").then(res => {
				this.$data.userInfo = res.data
				if(!this.$data.userInfo.wechatQrCode){
					this.$data.userInfo.wechatQrCode = require("../assets/add.svg")
				}else{
					this.$data.userInfo.wechatQrCode = this.ser.ctx + res.data.wechatQrCode
				}
				if(!this.$data.userInfo.alipayQrCode){
					this.$data.userInfo.alipayQrCode = require("../assets/add.svg")
				}else{
					this.$data.userInfo.alipayQrCode = this.ser.ctx + res.data.alipayQrCode
				}
			}).finally(() => {
				setTimeout(() => {
					mdui.mutation()
				}, 100)
			})
		}
	}
</script>

