<template>
	<div>
		<title-bar title="邀请注册" back-btn="true"></title-bar>
		<div class="content-box-title-bottom">
			<img src="../assets/orange.svg" style="width: 100%">
			<div class="inv-box">
				<vue-qr :text="qrcodePath" :size="200" :margin="0"></vue-qr>
				<div class="split">
					<div class="left"></div>
					<div class="right"></div>
				</div>
				<p>邀请码</p>
				<h1>{{userInfo.invCode}}</h1>
			</div>
		</div>
	</div>
</template>
<script>
	import titleBar from './titleBar.vue'
	import VueQr from 'vue-qr'

	export default {
		name: "share",
		components:{
			titleBar,
			VueQr,
		},
		data(){
			return {
				userInfo: {},
				qrcodePath: '',
			}
		},
		created() {
			this.$data.userInfo = this.$route.params
			let url = location.href
			url = url.substring(0, url.indexOf("/", 9)) + "/#/login?invCode=" + this.$data.userInfo.invCode
			this.$data.qrcodePath = url
		}
	}
</script>

