<template>
	<div>
		<title-bar title="资产明细" backBtn="true"></title-bar>
		<div class="content-box-title-bottom">
			<ul class="mdui-list">
			  <div v-for="item in detailList">
				  <li class="mdui-list-item mdui-ripple">
				    <div class="mdui-list-item-avatar">
						<img src="../assets/money.svg" v-if="!item.isGoods"/>
						<img :src="item.picture" v-if="item.isGoods"/>
					</div>
				    <div class="mdui-list-item-content">
				      <div class="mdui-list-item-title">{{item.goodsName}}</div>
				      <div class="mdui-list-item-text mdui-list-item-one-line">{{item.createTime}}</div>
				    </div>
					<div style="float: right; width: 100px; text-align: right;">
						<div :class="{'red-money': item.isAddMoney}">{{item.money}}</div>
						<div style="margin-top: 5px; font-size: 12px;">{{item.type}}</div>
					</div>
				  </li>
				  <li class="mdui-divider-inset mdui-m-y-0"></li>
			  </div>
			</ul>
		</div>
		<api-loading :class="{'loading-open': loadingShow}"></api-loading>
	</div>
</template>

<script>
	import titleBar from './titleBar.vue'
	import apiLoading from './apiLoading.vue'
	
	
	export default {
	  name: 'persionDetail',
	  components:{
		  titleBar,
		  apiLoading
	  },
	  methods:{
		  getStr(str, start, end) {
			  let startIndex = str.indexOf(start) + start.length
			  let endIndex = str.indexOf(end, startIndex )
			  let length = endIndex - startIndex;
		      return str.substr(startIndex, length)
		  }
	  },
	  data(){
		  return {
			  detailList: [],
			  loadingShow: false
		  }
	  }, 
	  created(){
		  this.$data.loadingShow = true
		  this.ser.get("api/v1/mobile/assets").then(res => {
			  this.$data.detailList = []
			  for(var i in res.data){
				  let detail = res.data[i]
				  // 买入
				  if(detail.type == '6' || detail.type == '8'){
					  this.$data.detailList.push({
						  createTime: detail.createTime,
						  goodsName: detail.goodsName,
						  type: "资产转入",
						  money: "+" + detail.price,
						  isAddMoney: true,
						  isGoods: true,
						  picture: this.ser.ctx + detail.picture
					  })
				  }
				  // 卖出
				  if(detail.type == '7' || detail.type == '10'){
					  this.$data.detailList.push({
						  createTime: detail.createTime,
						  goodsName: detail.goodsName,
						  type: "资产转出",
						  money: "-" + detail.price,
						  isAddMoney: false,
						  isGoods: true,
						  picture: this.ser.ctx + detail.picture
					  })
				  }
				  // 首充奖励
				  if(detail.type == '11'){
					  this.$data.detailList.push({
						  createTime: detail.createTime,
						  goodsName: '升值券',
						  type: "首充奖励",
						  money: "+" + this.getStr(detail.content, "首冲奖励", "升值券已入账"),
						  isAddMoney: true,
						  isGoods: false
					  })
				  }
				  if(detail.type == '13'){
					  this.$data.detailList.push({
						  createTime: detail.createTime,
						  goodsName: detail.goodsName,
						  type: "使用升值券",
						  money: "+" + this.getStr(detail.content, "资产价值已增加[", "]元"),
						  isAddMoney: true,
						  isGoods: true,
						  picture: this.ser.ctx + detail.picture
					  })
				  }
				  if(detail.type == '18'){
					  this.$data.detailList.push({
						  createTime: detail.createTime,
						  goodsName: '手续费退还',
						  type: "预约/抢购费用退还",    
						  money: "+" + this.getStr(detail.content, "[", "]"),
						  isAddMoney: true,
						  isGoods: false
					  })
				  }
				  if(detail.type == '17'){
					  this.$data.detailList.push({
						  createTime: detail.createTime,
						  goodsName: this.getStr(detail.content, "您的资产", "价值已") + "->" + this.getStr(detail.content, "升级为", ",当前") ,
						  type: "资产升级",    
						  money: "+" + this.getStr(detail.content, "当前价值[", "]元"),
						  isAddMoney: true,
						  isGoods: true,
						  picture: this.ser.ctx + detail.picture
					  })
				  }
			  }
		  }).finally(() => {
			  this.$data.loadingShow = false
		  })
	  }
	}
</script>

