<template>
	<div>
		<title-bar title="实名认证" backBtn="true"></title-bar>
		<div class="content-box-title-bottom">
			<img class="id-card-icon" src="../assets/idcard-auth.svg">
			<div class="id-card-text">完成实名认证方可抢购资产， 未进行实名认证之前， 您的资产以及您的个人权利将无法保障。</div>
			<div style="padding: 20px;">
				<div class="mdui-textfield mdui-textfield-floating-label">
					<label class="mdui-textfield-label">请输入姓名</label>
					<input class="mdui-textfield-input" type="text" name="name" autocomplete="off" v-model="userInfo.name" />
				</div>
				<div class="mdui-textfield mdui-textfield-floating-label">
					<label class="mdui-textfield-label">请输入身份证号</label>
					<input class="mdui-textfield-input" type="text" name="idcard" autocomplete="off" v-model="userInfo.idcard" />
				</div>
				<div style="margin-top: 30px;">
					<button class="mdui-btn mdui-btn-raised mdui-ripple mdui-color-theme-accent mdui-btn-block" @click="doAuth">提交</button>
				</div>
			</div>
			<api-loading :class="{'loading-open': loadingShow}"></api-loading>
		</div>
	</div>
</template>

<script>
	import titleBar from './titleBar.vue'
	import apiLoading from './apiLoading.vue'
	
	
	export default {
	  name: 'persionIdcard',
	  components:{
		  titleBar,
		  apiLoading,
	  },
	  data(){
		  return {
			  userInfo: {},
			  loadingShow: false
		  }
	  },
	  created() {
	  	// this.$data.loadingShow = true
	  	this.ser.get("api/v1/mobile/user/info").then(res => {
	  		this.$data.userInfo = res.data
	  	}).finally(() => {
	  		// this.$data.loadingShow = false
			setTimeout(() => {
				mdui.mutation()
			}, 100)
	  	})
	  },
	  methods:{
		  doAuth(){
			  const reqData = {
				  name: this.$data.userInfo.name,
				  idcard: this.$data.userInfo.idcard
			  }
			  if(!reqData.name){
				  mdui.snackbar("请输入真实姓名")
				  return
			  }
			  if(!reqData.idcard){
				  mdui.snackbar("请输入身份证号码")
				  return
			  }
			  this.$data.loadingShow = true
			  this.ser.post("api/v1/mobile/user/auth", reqData).then(res => {
			  	mdui.snackbar("实名认证提交成功")
			  }).finally(() => {
			  	this.$data.loadingShow = false
			  })
		  }
	  }
	}
</script>

