    <div>
        <title-bar title="我的分红" backBtn="true"></title-bar>
        <div class="content-box-title-bottom">
            <div class="money-block">
                <div>总持有(元)</div>
                <div class="money-revalue" v-text="userInfo.returnMoney">998.99</div>
            </div>
            <div class="text-block">
                说明: 分红金是您的下级每次卖出资产时, 系统会按相关比例分润入账到您的账户中, 在这里可以直接进行提现.
                <br/><br/>
                分红金的获得比率每个地区代理或团队长所在的团队会有所不同, 具体可咨询您的上级.
                <br/><br/>
                不同地区提现到账时间会有差异, 通常不会超过一天. 具体可咨询您的上级. 建议提交提现申请后直接联系上级催单.
