<script>
    import titleBar from './titleBar.vue'
    import apiLoading from './apiLoading.vue'

    export default {
        name: "persionRedMoney",
        data(){
            return {
                loadingShow: false,
                showLayer: false,
                useMoneys: '',
                buyGoodsInfo: [],
                selectGoods: 0,
                userInfo: {}
            }
        },
        components:{
            titleBar,
            apiLoading,
        },
        methods: {
            withdrawal(){
                this.$data.loadingShow = true
                this.ser.post("api/v1/mobile/assets/withdrawal").then(res => {
                    mdui.snackbar("提现申请提交成功")
                    this.$data.userInfo.returnMoney = 0
                }).finally(() => {
                    this.$data.loadingShow = false
                })
            },
        },
        created() {
            this.$data.userInfo = JSON.parse(localStorage.getItem("user"))
        }
    }
</script>

