<template>
	<div class="news-box">
		<div class="top-inner-shadow-top"></div>
		<div class="top-inner-shadow-bottom"></div>
		<ul id="con1" ref="con1" :class="{anim:animate==true}">
			<li class="news-title" v-for="item in items">{{item}}</li>
		</ul>
	</div>
</template>

<script>
	export default {
		name: "news",
		data: function (){
			return {
				animate:false,
				items: [
					"用户[***6451]抢购价值555.55的[贡桔]",
					"用户[***3388]抢购价值3487.55的[沙糖桔]",
					"用户[***2201]抢购价值12451.74的[沙糖桔]",
					"用户[***5273]抢购价值265.12的[贡桔]"
				]
			}
		},
		created(){
			// 这里请求数据
			this.timer = setInterval(this.scroll,1000)
		},
		methods: {
			scroll(){
				let con1 = this.$refs.con1;
				if(con1){
					con1.style.marginTop='-25px';
				}
				this.animate=true;
			    setTimeout(()=>{      
					   this.items.push(this.items[0]);  
					   this.items.shift();          
					   if(con1){
						   	con1.style.marginTop='0px';
					   }
					   this.animate=false; 
			    },500)
			}
		},
		beforeDestroy(){
		  clearInterval(this.timer);
		},
	}
</script>

<style>
	.news-box{
		height: 70px;
		width: calc(100% - 20px);
		background-color: #ff9800!important;
		border-radius: 10px;
		margin-left: 10px;
		margin-top: 10px;
		box-shadow: rgba(0,0,0,.8) 2px 2px 10px -5px;
		overflow-y: hidden;
	}
	.news-title {
		color: white;
		padding-left: 10px;
		list-style: none;
		line-height: 25px;
		height: 25px;
	}
	.news-box ul{
		margin: 0;
		padding: 0;
	}
	.anim{
		transition: all 0.5s;
	}
	.top-inner-shadow-top{
		background: linear-gradient(#ff9800,rgba(255,152,0,0));
		height: 15px;
		width: calc(100% - 20px);
		position: absolute;
		z-index: 1;
		border-radius: 10px;
	}
	.top-inner-shadow-bottom{
		background: linear-gradient(rgba(255,152,0,0), #ff9800);
		height: 15px;
		width: calc(100% - 20px);
		position: absolute;
		z-index: 1;
		border-radius: 10px;
		margin-top: 55px;
	}
</style>
