<template>
	<div>
		<title-bar title="系统消息" backBtn="true"></title-bar>
		<div class="content-box-title-bottom">
			<div style="margin-top: 20px; text-align: center;" v-if="messageList.length === 0">暂无数据</div>
			<ul class="mdui-list">
			  <div v-for="item in messageList">
				  <li class="mdui-list-item mdui-ripple" @click="goToInfo(item)" >
				    <div class="mdui-list-item-avatar"><img src="../assets/noti.svg"/></div>
				    <div class="mdui-list-item-content">
				      <div class="mdui-list-item-title">系统通知</div>
				      <div class="mdui-list-item-text mdui-list-item-one-line">{{item.content}}</div>
				    </div>
				  </li>
				  <div>
					  <div style="margin-top: -10px; font-size: 10px; margin-left: 73px;">
						  <span>{{item.createTime}}</span>
						  <span style="color: white; background-color: red; padding-left: 3px; padding-right: 3px; margin-left: 5px; border-radius: 3px;" v-if="!item.readed">new</span>
					  </div>
				  </div>
				  <li class="mdui-divider-inset mdui-m-y-0"></li>
			  </div>
			</ul>
		</div>
		<api-loading :class="{'loading-open': loadingShow}"></api-loading>
	</div>
</template>

<script>
	import titleBar from './titleBar.vue'
	import apiLoading from './apiLoading.vue'
	
	
	export default {
		name: 'persionMessage',
		components:{
			titleBar,
			apiLoading,
		},
		data(){
			return {
				messageList: [
				],
				loadingShow: false,
			}
		},methods:{
			goToInfo(message){
			  this.$router.push({
				  name: "persionMessageInfo",
				  params: {
				  	  messageInfo: message
				  }
			  });
			}
		},
		created() {
			this.$data.loadingShow = true
			this.ser.get("api/v1/mobile/message").then(res => {
				this.$data.messageList = res.data
			}).finally(() => {
				this.$data.loadingShow = false
			})
		}
	}
</script>

