<template>
    <div>
        <span class="pattern left"><img src="../assets/download-left.jpg"></span>
        <span class="pattern right"><img src="../assets/download-right.jpg"></span>
        <div class="out-container">
            <img src="../assets/login-bg.svg" class="icon-img">
            <p class="app-title">橘园</p>
            <div class="line"></div>
            <div class="version-info">
                <p>版本: v1.0.1</p>
                <p>更新于: 2020-06-14 00:00:00</p>
            </div>
            <div class="download-box">
                <div><button class="mdui-btn mdui-color-theme-accent mdui-ripple" @click="download(1)">安卓下载</button></div>
                <div><button class="mdui-btn mdui-color-theme-accent mdui-ripple" @click="download(2)">IOS下载</button></div>
            </div>
        </div>
        <div class="download-shad" v-if="showShad">
            <h2>请点击右上角</h2>
            <h2>选择在浏览器中打开</h2>
        </div>
    </div>
</template>

<script>
    export default {
        name: "download",
        data(){
            return {
                showShad: false
            }
        },
        methods: {
            isWeiXin(){
                let ua = window.navigator.userAgent.toLowerCase();
                if(ua.match(/MicroMessenger/i) == 'micromessenger'){
                    return true;
                }else{
                    return false;
                }
            },
            isQQ() {
                let isQ = navigator.userAgent.toLowerCase().indexOf("qq") > -1
                return isQ
            },
            download(type){
                if (type === 1){
                    // 安卓
                    location.href = '../download/android.apk'
                }else{
                    // IOS
                    location.href = '../download/ios.mobileconfig'
                }
            }
        },
        created() {
            this.$data.showShad = (this.isWeiXin() || this.isQQ())
        }
    }
</script>

