<template>
	<div>
		<title-bar title="橘子园"></title-bar>
		<div class="content-box-title-bottom">
			<div class="swipper-background">
				<div class="background"></div>
				<div class="background-bottom"></div>
				<div class="background-bottom-border"></div>
				<div class="content animate__animated animate__slideInDown">
					<div class="header-img">
						<img src="../assets/head.svg" />
						<span v-text="userInfo.username">17744416451</span>
					</div>
					<summoney :userInfo="$data.userInfo"></summoney>
				</div>
			</div>
			<assets-nav :userInfo="$data.userInfo"></assets-nav>
			<settings></settings>
			<button class="mdui-btn mdui-btn-icon mdui-color-theme-accent mdui-ripple share-btn mdui-btn-raised" @click="gotoShare()"><i class="mdui-icon material-icons">share</i></button>
		</div>
		<api-loading :class="{'loading-open': loadingShow}"></api-loading>
		<bottom-nav act="persion"></bottom-nav>
	</div>
</template>

<script>
	import titleBar from './titleBar.vue'
	import bottomNav from './bottomNav.vue'
	import summoney from './summoney.vue'
	import settings from './settings.vue'
	import assetsNav from './assetsNav.vue'
	import apiLoading from './apiLoading.vue'
	
	export default {
		name: "persion",
		components:{
			titleBar,
			bottomNav,
			summoney,
			settings,
			assetsNav,
			apiLoading
		},
		data(){
			return {
				userInfo: {},
				loadingShow: false
			}
		},
		created() {
			this.$data.loadingShow = true
			this.ser.get("api/v1/mobile/user/info").then(res => {
				this.$data.userInfo = res.data
			}).finally(() => {
				this.$data.loadingShow = false
			})
		},
		methods: {
			gotoShare(){
				this.$router.push({name: "share", params: this.$data.userInfo})
			}
		}
	}
</script>

