<template>
    <div>
        <title-bar title="平台介绍" backBtn="true"></title-bar>
        <div class="content-box-title-bottom">
            <h3>概括</h3>
            <p>
                本平台是基于伪区块技术实现的一套资产交易系统, 玩家和玩家之间进行交易专区利润. 平台本身不会压本金, 交易完成后本金+利润一次性结清.
                玩家通过抢购(租借)果树,并获得合约期间果树产生的利润, 合约期结束后, 果树将转让给下一位玩家.
            </p>
            <h3>果树</h3>
            <p>
                果树是该系统中重要的组成部分, 每一棵果树都可成为玩家的资产, 果树在合约期内可以产生果实, 果实会自动转换为资产存入玩家的账户.
            </p>
            <h3>肥料</h3>
            <p>
                商品之间的交易由玩家与玩家之间操作, 系统会收取响应手续费用. 这里的肥料则指手续费. 玩家的与果树之间均为租借关系, 合约时间越长, 肥料消耗也就越多.
            </p>
            <p>
                玩家需要支付一定数量的肥料之后, 便可以进行果树的预约和抢购. 提前预约好果树后, 抢购时无需再额外支付肥料. 若不预约直接抢购的话, 平台将收取双倍肥料.
            </p>
            <h3>肥料充值</h3>
            <p>
                为自己充值: 玩家可直接找上级充值肥料, 通过上级充值肥料时, 不会享受平台活动的策略, 但上级玩家可以针对自己的下级开设自己的充值活动规则. 团队长则可以直接与管理员联系进行充值, 享受平台活动策略.
            </p>
            <p>
                为下级充值: 当玩家自身拥有一定数目的肥料以后, 可以为下级用户进行充值, 在[个人中心] -> [团队管理]中, 点击用户头像即可为下级用户充值. 充值金额不得大于玩家已有金额.
            </p>
            <h3>资产与收益</h3>
            <p>
                玩家持有中的果树称之为资产, 每天晚上0点, 系统会根据当前果树当天产生的价值进行叠加资产. 当资产转让出去后, 在合约期叠加的资产则为玩家赚取的收益.
            </p>
            <h3>升值券</h3>
            <p>
                升值券是本系统中的亮点功能. 升值券由当前玩家的下级玩家二次充值时按一定比例获得. 具体获得比例因团队而异. 所以选择一个好的团队长是重中之重.
                升值券可用于直接增加玩家持有中果树的价值.
            </p>
            <p style="color:gray;">
                举例: 玩家抢购价值200元的果树, 合约收益为5%, 一天后合约结束, 那么次日玩家应获得本金+利润为210元. 若玩家对该果树使用10元升值券, 则次日应得收益为(210 + 10)即220元.
            </p>
            <h3>分红</h3>
            <p>
                当玩家每次获得收益时, 那么这个玩家的邀请人(也就是玩家的上级用户)会获得一定比例的分红奖励, 分红金可以直接体现, 无需转换为果树卖出.
            </p>
            <p>
                当玩家的直属下级达到一定数量时, APP会引导玩家与管理员取得联系, 可双方协商分红比例, 甚至双方交换条件以将利益最大化.
            </p>
            <h3>收款码</h3>
            <p>
                由于本系统不存在任何聚资风险, 所有交易都是玩家和玩家之间进行的, 所以每个玩家都应上传自己的收款码以方便资产转出. 强烈建议上传两种收款码已被不足之需.
            </p>
            <h3>实名认证</h3>
            <p>
                为保大家交易安全, 防止别有用心的薅盘手破坏玩家之间的正常交易, 本APP必须进行实名认证后方可正常交易.
            </p>
            <h3>客服工单</h3>
            <p>
                玩家可以通过在客服中心提交工单的方式跳过上级, 直接与与管理员直接取得沟通. 该通道仅限10人以上(含10人)的团队长.
            </p>
