<template>
	<div>
		<title-bar title="我的预约" backBtn="true"></title-bar>
		<div class="content-box-title-bottom">
			<ul class="mdui-list">
				<div v-if="makeList.length == 0" style="padding: 10px;text-align: center">
					暂无数据
				</div>
			  <div v-for="item in makeList">
				  <li class="mdui-list-item mdui-ripple" v-if="item.statusStr == '可抢购'" @click="buyGoods(item.templateId, item.picture)">
				    <div class="mdui-list-item-avatar">
						<img :src="ser.ctx + item.picture"/>
					</div>
				    <div class="mdui-list-item-content">
				      <div class="mdui-list-item-title">{{item.goodsName}}</div>
				      <div class="mdui-list-item-text mdui-list-item-one-line">{{item.makeDate}}</div>
				    </div>
					<div style="float: right; width: 100px; text-align: right;">
						<div style="margin-top: 5px; font-size: 12px;">{{item.statusStr}}</div>
					</div>
				  </li>
				  <li class="mdui-list-item mdui-ripple" v-if="item.statusStr != '可抢购'">
				    <div class="mdui-list-item-avatar">
						<img :src="ser.ctx + item.picture"/>
					</div>
				    <div class="mdui-list-item-content">
				      <div class="mdui-list-item-title">{{item.goodsName}}</div>
				      <div class="mdui-list-item-text mdui-list-item-one-line">{{item.makeDate}}</div>
				    </div>
						<div style="float: right; width: 100px; text-align: right;">
							<div style="margin-top: 5px; font-size: 12px;">{{item.statusStr}}</div>
						</div>
				  </li>
				  <li class="mdui-divider-inset mdui-m-y-0"></li>
			  </div>
			</ul>
		</div>
		<loading-buy :open="showBuy" :closeFun="stopBuy"></loading-buy>
		<api-loading :class="{'loading-open': loadingShow}"></api-loading>
		<buy-result :open="showResult" :closeFun="closeResult" :buyResult="buyResult" :goodsImage="goodsImage" :price="goodsPrice"></buy-result>
	</div>
</template>

<script>
	import titleBar from './titleBar.vue'
	import loadingBuy from './loading-buy.vue'
	import apiLoading from './apiLoading.vue'
	import buyResult from './buyResult.vue'
	
	
	export default {
	  name: 'persionMake',
	  components:{
		  titleBar,
		  loadingBuy,
		  apiLoading,
		  buyResult
	  },
	  methods:{
		  closeResult(){
		  	this.$data.showResult = false
		  },
		  buyGoods(templateId, goodsImage){
		  	this.$data.showBuy = true
		  	this.$data.buyCount = 0;
		  	this.$data.goodsImage = this.ser.ctx + goodsImage;
		  	this.$data.goodsPrice = "";
		  	this.startBuy(templateId, goodsImage)
		  },
		  startBuy(templateId, goodsImage){
		  	this.$data.buyCount ++
		  	if(this.$data.buyCount > 10){
		  		//达到10次时， 不是自动抢购就自动结束
		  		if(!localStorage.getItem("authBuy")){
		  			this.$data.showBuy = false
		  			this.$data.buyResult = false
		  			this.$data.showResult = true
		  			return
		  		}
		  	}
		  	this.ser.post("api/v1/mobile/goods/" + templateId + "/buy").then(res => {
		  		this.$data.goodsPrice = res.data.responseBody.price + "";
		  		this.$data.showBuy = false
		  		this.$data.buyResult = true
		  		this.$data.showResult = true
				this.listMakes()
		  	}).catch((res) => {
		  		if (res.data.message === "您今天已经抢购该资产了，不能重复抢购"){
		  			this.$data.showBuy = false
		  		}
		  		if(res.data.message.indexOf("当前时间段不能") !== -1){
		  			this.$data.showBuy = false
		  			this.$data.buyResult = false
		  			this.$data.showResult = true
		  		}
		  	}).finally(()=>{
		  		if(this.$data.showBuy){
		  			let num = this.randomNum(1000, 10000)
		  			setTimeout(() => {
		  				this.startBuy(templateId)
		  			}, num);
		  		}
		  	})
		  	
		  },
		  randomNum(minNum,maxNum){ 
		      switch(arguments.length){ 
		          case 1: 
		              return parseInt(Math.random()*minNum+1,10); 
		          break; 
		          case 2: 
		              return parseInt(Math.random()*(maxNum-minNum+1)+minNum,10); 
		          break; 
		              default: 
		                  return 0; 
		              break; 
		      } 
		  },
		  stopBuy(){
		  	this.$data.showBuy = false
		  },
		  listMakes(){
			  this.$data.loadingShow = true
			  this.ser.get("api/v1/mobile/goods/makes").then(res => {
				  this.$data.makeList = res.data
			  }).finally(() => {
				  this.$data.loadingShow = false
			  })
		  }
	  },
	  data(){
		  return {
			  makeList: [],
			  loadingShow: false,
			  showBuy: false,
			  buyCount: 0,
			  showResult: false,
			  buyResult: false,
			  goodsImage: "http://156.96.148.124/comm/display/2020/05/17/eaded2ef80db49e4b8dfba87abfd0e4c",
			  goodsPrice: "200"
		  }
	  }, 
	  created(){
		  this.listMakes()
	  }
	}
</script>

