                <div class="money-revalue" v-text="redPackage">0</div>
                <div style="float: right; margin-right: 20px; margin-top: -35px;">
                    <button class="mdui-btn mdui-btn-raised mdui-ripple" @click="openWithdrawal">提现</button>
                </div>
            </div>

            <div>
                <div class="pond-head" v-on:click="init">
                    <img src="../assets/lottery/pond-head.png" alt="">
                </div>
                <div style="display: block; margin-top: -100px; color: white; text-align: center; text-shadow: black 1px 1px 5px">当前剩余抽奖次数: <span v-text="lastLotteryCount"></span></div>
                <div class="container-out">
                    <div class="container-in">
                        <div class="content-out" v-for="(box,index) in list"
                             :style="{left:box.left+'px',top:box.top+'px','background-color':index==indexSelect?colorAwardSelect:colorAwardDefault}">
                            <img class="pond-name-img" :src="box.image_url" alt="">
