                            <div class="continue" v-on:click="conTinue">继续抽奖</div>
                        </div>
                    </div>
                </div>
                <div class="prize" v-show="prize==2">
                    <div class="prize-box">
                        <img class="prize-img2" src="../assets/lottery/pond-empty.png" alt="">
                        <div class="prize-msg">
                            <div>谢谢参与</div>
                            <div>再换个姿势抽奖</div>
                            <div class="continue" v-on:click="conTinue">继续抽奖</div>
                        </div>

                    </div>
                </div>
                <div class="men" v-show="men"></div>
            </div>
            <div style="padding: 10px; color: white;">
                <div style="border-bottom: white solid 1px; text-align: center; height: 7px">
                    <div style="background-color: red; display: inline-block; width: 80px;">活动规则</div>
                </div>
                <p>1. 新注册用户实名并绑定收款码后赠送一次抽奖机会</p>
                <p>2. 注册后, 首充一次, 每日赠送一次抽奖机会</p>
                <p>3. 注册后, 每邀请一个新用户, 获得一次抽奖机会</p>
                <p>4. 除现金红包可以直接提现外, 其他虚拟物品自动领取</p>
                <p>5. 相关虚拟物品使用说明请<a href="javascript:;" @click="gotoDscp">戳这里</a></p>
                <p>6. 若对本活动有任何疑问请咨询您的邀请人或提交客服工单.</p>
            </div>
        </div>
        <layer v-model="showLayer" :btn="['确定', '放弃']" @sure="withdrawal" @cancel="closeBox">
            <div class="alert-content" v-if="withdrawalCount === 0">
                <div class="item" :class="{'select': selectMoney===1}" @click="selectMoneyClick(1, 0.3)">0.3元</div>
                <div class="item" :class="{'select': selectMoney===2}" @click="selectMoneyClick(2, 0.5)">0.5元</div>
