                <div class="item" :class="{'select': selectMoney===5}" @click="selectMoneyClick(5, 50)">50元</div>
                <div class="item" :class="{'select': selectMoney===6}" @click="selectMoneyClick(6, 100)">100元</div>
                <div class="item" :class="{'select': selectMoney===7}" @click="selectMoneyClick(7, 200)">200元</div>
                <div class="item" :class="{'select': selectMoney===8}" @click="selectMoneyClick(8, 500)">500元</div>
            </div>
            <div class="alert-content" v-if="withdrawalCount === 1">
                <div class="item" :class="{'select': selectMoney===2}" @click="selectMoneyClick(2, 0.5)">0.5元</div>
                <div class="item" :class="{'select': selectMoney===3}" @click="selectMoneyClick(3, 1)">1元</div>
                <div class="item" :class="{'select': selectMoney===4}" @click="selectMoneyClick(4, 10)">10元</div>
                <div class="item" :class="{'select': selectMoney===5}" @click="selectMoneyClick(5, 50)">50元</div>
                <div class="item" :class="{'select': selectMoney===6}" @click="selectMoneyClick(6, 100)">100元</div>
                <div class="item" :class="{'select': selectMoney===7}" @click="selectMoneyClick(7, 200)">200元</div>
                <div class="item" :class="{'select': selectMoney===8}" @click="selectMoneyClick(8, 500)">500元</div>
            </div>
            <div class="alert-content" v-if="withdrawalCount > 1">
                <div class="item" :class="{'select': selectMoney===5}" @click="selectMoneyClick(5, 50)">50元</div>
                <div class="item" :class="{'select': selectMoney===6}" @click="selectMoneyClick(6, 100)">100元</div>
                <div class="item" :class="{'select': selectMoney===7}" @click="selectMoneyClick(7, 200)">200元</div>
                <div class="item" :class="{'select': selectMoney===8}" @click="selectMoneyClick(8, 500)">500元</div>
            </div>
            <div style="text-align: left">
                合计: <span v-text="selectRedMoney"></span>红包金
            </div>
        </layer>
        <api-loading :class="{'loading-open': loadingShow}"></api-loading>
