</template>

<script>
    import TitleBar from "./titleBar";
    import ApiLoading from "./apiLoading";
    import BottomNav from "./bottomNav";
    export default {
        name: "lottery",
        components: {BottomNav, ApiLoading, TitleBar},
        data(){
            return {
                list: [
                    { id: 1, type: 1, name: '4千现金红包', image_url: require("../assets/lottery/red-package.svg") },
                    { id: 2, type: 2, name: '谢谢参与', image_url: require("../assets/lottery/xiexie.svg") },
                    { id: 3, type: 3, name: '10元肥料', image_url: require("../assets/lottery/feiliao.svg") },
                    { id: 4, type: 4, name: '20元肥料', image_url: require("../assets/lottery/feiliao.svg") },
                    { id: 5, type: 5, name: '2万现金红包', image_url: require("../assets/lottery/red-package.svg") },
                    { id: 6, type: 6, name: '5元升值券', image_url: require("../assets/upMoney.svg") },
                    { id: 7, type: 7, name: '10元升值券', image_url: require("../assets/upMoney.svg") },
                    { id: 8, type: 8, name: '谢谢参与', image_url: require("../assets/lottery/xiexie.svg") },
                ],
                circleList: [],
                colorCircleFirst: "#F12416",
                colorCircleSecond: "#FFFFFF",
                colorAwardDefault: "#F5F0FC",
                colorAwardSelect: "#ffe400",
                btntop: 0,//按钮的样式
                time: '',//定时器
                indexSelect: 0,//奖品下标
                lottert: 0,//中奖下标
                prize:0,//是否中奖
                prize_name:'',//奖品名字
                men:false,
                redPackage: 0,
                showLayer: false,
                selectMoney: 1,
                selectRedMoney: 0,
                withdrawalCount: 0,
                lastLotteryCount: 0,
                loadingShow: false,
            }
        },
        created() {
            this.init()
            this.$data.loadingShow = true
            this.ser.get("api/v1/mobile/user/info").then(res => {
                this.$data.redPackage = res.data.redPackage
                this.$data.withdrawalCount = res.data.redPackageWithdrawalCount
                this.$data.lastLotteryCount = res.data.lastLotteryCount
            }).finally(() => {
                this.$data.loadingShow = false
            });
        },
        methods: {
            openWithdrawal(){
                if(this.$data.withdrawalCount === 0){
                    this.selectMoneyClick(1, 0.3)
                }else if(this.$data.withdrawalCount === 1){
                    this.selectMoneyClick(2, 0.5)
                }else if(this.$data.withdrawalCount > 1){
                    this.selectMoneyClick(5, 50)
                }
                this.$data.showLayer = true
            },
            closeBox(){
                this.$data.showLayer = false
            },
            withdrawal(){
                this.$data.showLayer = false
                this.$data.loadingShow = true
                this.ser.post("api/v1/mobile/user/withdrawal-red-package", {
                    redPackage: this.$data.selectRedMoney
                }).then(res => {
                    this.$data.redPackage = this.$data.redPackage - this.$data.selectRedMoney
                    mdui.snackbar("提现成功")
                }).finally(() => {
                    this.$data.loadingShow = false
                })
            },
            gotoDscp(){
                this.$router.push("/dscp")
            },
            init: function () {
                var ts = this;
                var list = this.list;
                var left = 9;
                var top = 9;

                for (var i = 0; i < 8; i++) {
                    if (i == 0) {
                        left = 9;
                        top = 9;
                    } else if (i < 3 && i != 0) {
                        top = top;
                        left = left + 98 + 4;
                    } else if (i >= 3 && i < 5) {
                        left = left;
                        top = top + 79 + 4;
                    } else if (i >= 5 && i < 7) {
                        left = left - 98 - 4;
                        top = top;
                    } else if (i >= 7 && i < 8) {
                        left = left;
                        top = top - 79 - 4;
                    }
                    list[i].top = top;
                    list[i].left = left;
                }
                this.list = list;
                var cleft = 4;//e
                var ctop = 4;//a
                var dian = [];
                for (var j = 0; j < 24; j++) {
                    if (j == 0) {
                        cleft = 4;
                        ctop = 4;
                    } else if (j < 6) {
                        ctop = 2;
                        cleft += 55;
                    } else if (j == 6) {
                        ctop = 2;
                        cleft = 330;
                    } else if (j < 12) {
                        ctop += 46;
                        cleft = 331.5;
                    } else if (j == 12) {
                        ctop = 272.5;
                        cleft = 330;
                    } else if (j < 18) {
                        ctop = 275;
                        cleft -= 55;
                    } else if (j == 18) {
                        ctop = 273;
                        cleft = 5;
                    } else {
                        if (!(j < 24)) return;
                        ctop -= 46, cleft = 2.5;
                    }
                    dian.push({
                        topCircle: ctop,
                        leftCircle: cleft
                    })
                }

                this.circleList = dian;
                setInterval(function () {
                    if (ts.colorCircleFirst == "#FFFFFF") {
                        ts.colorCircleFirst = "#F12416";
                        ts.colorCircleSecond = "#FFFFFF";
                    } else {
                        ts.colorCircleFirst = "#FFFFFF";
                        ts.colorCircleSecond = "#F12416";
                        ts.btntop = 0;
                    }
                }, 900)
                this.time = setInterval(function () {
                    if (ts.btntop == 0) {
                        ts.btntop = -3;
                    } else {
                        ts.btntop = 0;
                    }
                }, 900)
            },
            startBtn: function (e) {
                this.$data.lastLotteryCount = this.$data.lastLotteryCount - 1
                clearInterval(this.time);
                this.men=true;
                this.btntop = 0;
                this.lottert = 0;
                var ts = this;
                var i = this.indexSelect;
                var list = this.list;
                var time = null;
                var s = 0;

                time = setInterval(function () {
                    i++;
                    i %= 8;
                    s += 30;
                    ts.indexSelect = i;
                    if (ts.lottert > 0 && i + 1 == ts.lottert) {
                        clearInterval(time);
                        ts.time = setInterval(function () {
                            if (ts.btntop == 0) {
                                ts.btntop = -3;
                            } else {
                                ts.btntop = 0;
                            }
                        }, 900);
                        if (list[i].type == 2) {
                            ts.prize = 2;
                        } else {
                            ts.prize_name = list[i].name;
                            ts.prize = 1;
                        }

                    }
                }, 200 + s);

                this.$data.loadingShow = true
                this.ser.post("api/v1/mobile/user/lottery").then(res => {
                    setTimeout(() => {
                        ts.lottert = res.data.responseBody
                        if (ts.lottert == 1){
                            this.$data.redPackage = this.$data.redPackage + 4000
                        }else if(ts.lottert == 8){
                            this.$data.redPackage = this.$data.redPackage + 20000
                        }
                    }, 5000)
                }).finally(() => {
                    this.$data.loadingShow = false
                })

            },
            randomNum: function (minNum, maxNum) {
                switch (arguments.length) {
                    case 1:
                        return parseInt(Math.random() * minNum + 1, 10);
                        break;
                    case 2:
                        return parseInt(Math.random() * (maxNum - minNum + 1) + minNum, 10);
                        break;
                    default:
                        return 0;
                        break;
                }
            },
            conTinue:function(){
                this.men=false;
                this.prize=0;
            },
            selectMoneyClick(index, money){
                this.$data.selectMoney = index
                this.$data.selectRedMoney = money * 10000
            }
        }

    }
</script>

