<template>
	<div>
		<div style="margin-bottom: 70px;">
			<div class="list-box">
				<ul class="mdui-list">
				  <li class="mdui-list-item mdui-ripple" @click="gotoPath('/persion/idcard')">
				    <i class="mdui-list-item-icon mdui-icon material-icons">credit_card</i>
				    <div class="mdui-list-item-content">实名认证</div>
				    <i class="mdui-list-item-icon mdui-icon material-icons">chevron_right</i>
				  </li>
				  <li class="mdui-list-item mdui-ripple" @click="gotoPath('/persion/qrcode')">
				    <i class="mdui-list-item-icon mdui-icon material-icons">attach_money</i>
				    <div class="mdui-list-item-content">收款码</div>
				    <i class="mdui-list-item-icon mdui-icon material-icons">chevron_right</i>
				  </li>
					<li class="mdui-list-item mdui-ripple" @click="gotoPath('/persion/red-package-with')" v-if="redPackageMsgCount !== -1">
						<i class="mdui-list-item-icon mdui-icon material-icons">attach_money</i>
						<div class="mdui-list-item-content">红包提现</div>
						<span class="message-count" v-if="redPackageMsgCount > 0">{{redPackageMsgCount}}</span>
						<i class="mdui-list-item-icon mdui-icon material-icons">chevron_right</i>
					</li>
				</ul>
			</div>
			<div class="list-box">
				<ul class="mdui-list">
				  <li class="mdui-list-item mdui-ripple" @click="gotoPath('/persion/detail')">
				    <i class="mdui-list-item-icon mdui-icon material-icons">art_track</i>
				    <div class="mdui-list-item-content">资产明细</div>
				    <i class="mdui-list-item-icon mdui-icon material-icons">chevron_right</i>
				  </li>
				  <li class="mdui-list-item mdui-ripple" @click="gotoPath('/persion/message')">
				    <i class="mdui-list-item-icon mdui-icon material-icons">email</i>
				    <div class="mdui-list-item-content">系统消息</div>
					  <span class="message-count" v-if="newMessageCount > 0">{{newMessageCount}}</span>
				    <i class="mdui-list-item-icon mdui-icon material-icons">chevron_right</i>
				  </li>
				  <li class="mdui-list-item mdui-ripple" @click="gotoPath('/persion/group')">
				    <i class="mdui-list-item-icon mdui-icon material-icons">group</i>
				    <div class="mdui-list-item-content">我的团队</div>
				    <i class="mdui-list-item-icon mdui-icon material-icons">chevron_right</i>
				  </li>
				</ul>
			</div>
			<div class="list-box">
				<ul class="mdui-list">
				  <li class="mdui-list-item mdui-ripple">
				    <i class="mdui-list-item-icon mdui-icon material-icons">autorenew</i>
				    <div class="mdui-list-item-content">自动抢购</div>
				    <label class="mdui-switch">
				      <input type="checkbox" checked/>
				      <i class="mdui-switch-icon"></i>
				    </label>
				  </li>
				  <li class="mdui-list-item mdui-ripple">
				    <i class="mdui-list-item-icon mdui-icon material-icons">access_alarm</i>
				    <div class="mdui-list-item-content">抢购倒计时</div>
				    <label class="mdui-switch">
					  <input type="checkbox" checked/>
					  <i class="mdui-switch-icon"></i>
					</label>
				  </li>
				  <li class="mdui-list-item mdui-ripple">
				    <i class="mdui-list-item-icon mdui-icon material-icons">notifications_active</i>
				    <div class="mdui-list-item-content">声音提醒</div>
				    <label class="mdui-switch">
				      <input type="checkbox" checked/>
				      <i class="mdui-switch-icon"></i>
				    </label>
				  </li>
				</ul>
			</div>
			<div class="list-box">
				<ul class="mdui-list">
				  <li class="mdui-list-item mdui-ripple" @click="openRePswdBox">
				    <i class="mdui-list-item-icon mdui-icon material-icons">lock</i>
				    <div class="mdui-list-item-content">修改密码</div>
				    <i class="mdui-list-item-icon mdui-icon material-icons">chevron_right</i>
				  </li>
				  <li class="mdui-list-item mdui-ripple" @click="logout">
				    <i class="mdui-list-item-icon mdui-icon material-icons">power_settings_new</i>
				    <div class="mdui-list-item-content">退出</div>
				    <i class="mdui-list-item-icon mdui-icon material-icons">chevron_right</i>
				  </li>
				</ul>
			</div>
		</div>
		<layer v-model="showLayer" @sure="rePswd" @cancel="closeBox" :btn="['确定', '取消']">
			<div class="mdui-textfield mdui-textfield-floating-label alert-content">
				<label class="mdui-textfield-label">请输入新密码</label>
				<input class="mdui-textfield-input" v-model="newPassword" type="password"/>
			</div>
			<div class="mdui-textfield mdui-textfield-floating-label alert-content">
				<label class="mdui-textfield-label">重复输入新密码</label>
				<input class="mdui-textfield-input" v-model="checkNewPassword" type="password"/>
			</div>
		</layer>
	</div>
</template>

<script>

	import apiLoading from './apiLoading.vue'

	export default {
		name: "settings",
		data(){
			return {
				newMessageCount: 0,
				redPackageMsgCount: -1,
				showLayer: false,
				newPassword: '',
				checkNewPassword: '',
				loadingShow: false,
			}
		},
		components: {
			apiLoading,
		},
		created() {
			this.ser.get("api/v1/mobile/user/msg-count").then(res => {
				this.$data.newMessageCount = res.data.responseBody.sys
				this.$data.redPackageMsgCount = res.data.responseBody.redPackage
			})
		},
		methods:{
			openRePswdBox(){
				this.$data.showLayer = true
			},
			rePswd(){
				this.$data.showLayer = false
				if (!this.$data.newPassword){
					mdui.snackbar("新密码不能为空")
					return
				}
				if (this.$data.newPassword !== this.$data.checkNewPassword){
					mdui.snackbar("两次密码输入不一致")
					return
				}
				this.$data.loadingShow = true
				this.ser.put("api/v1/mobile/user/password", {
					password: this.$data.newPassword
				}).then(res => {
					this.$data.newPassword = ''
					this.$data.checkNewPassword = ''
					this.$router.replace({path: '/login'})
				}).finally(() => {
					this.$data.loadingShow = false
				})
			},
			closeBox(){
				this.$data.showLayer = false
			},
			gotoPath(path){
				this.$router.push({path: path})
			},
			logout(){
				this.ser.post("api/v1/mobile/user/logout").then(res => {
					this.$router.replace({path: "/login"})
				})
			}
		}
	}
</script>

