<template>
    <div>
        <title-bar title="红包提现申请" backBtn="true"></title-bar>
        <div class="content-box-title-bottom">
            <ul class="mdui-list">
                <ul class="mdui-list mdui-list-dense" mdui-collapse="{accordion: true}">
                    <li class="mdui-collapse-item"  v-for="item in msgList">
                        <div class="mdui-collapse-item-header mdui-list-item mdui-ripple">
                            <div class="mdui-list-item-avatar"><img src="../assets/head.svg"/></div>
                            <div class="mdui-list-item-content">
                                <div class="mdui-list-item-title" v-text="item.name"></div>
                                <div class="mdui-list-item-text mdui-list-item-one-line">
                                    <span v-text="(item.status === 0 ? '未处理 ' : '已处理 ') + item.createTime"></span>
                                    金额:<span v-text="item.money"></span>
                                </div>
                            </div>
                            <i class="mdui-collapse-item-arrow mdui-icon material-icons">keyboard_arrow_down</i>
                        </div>
                        <ul class="mdui-collapse-item-body mdui-list mdui-list-dense">
                            <li class="mdui-list-item mdui-ripple upload-payd-img" style="padding-left:2rem;" @click="showUserInfo(item.userId)">
                                <i class="mdui-list-item-icon mdui-icon material-icons">monetization_on</i>
                                <div class="mdui-list-item-content">查看手机号</div>
                            </li>
                            <li class="mdui-list-item mdui-ripple upload-payd-img" style="padding-left:2rem;" @click="howUserQrCode(item.userId)">
                                <i class="mdui-list-item-icon mdui-icon material-icons">monetization_on</i>
                                <div class="mdui-list-item-content">查看收款码</div>
                            </li>
                            <li class="mdui-list-item mdui-ripple upload-payd-img" style="padding-left:2rem;" @click="deleteMsg(item.id)">
                                <i class="mdui-list-item-icon mdui-icon material-icons">gavel</i>
                                <div class="mdui-list-item-content">删除</div>
                            </li>
                            <li class="mdui-list-item mdui-ripple upload-payd-img" style="padding-left:2rem;">
                                <i class="mdui-list-item-icon mdui-icon material-icons">gavel</i>
                                <div class="mdui-list-item-content">禁封/解封</div>
                            </li>
                        </ul>
                        <ul class="mdui-list"><li class="mdui-divider-inset mdui-m-y-0"></li></ul>
                    </li>
                </ul>
            </ul>
        </div>
        <api-loading :class="{'loading-open': loadingShow}"></api-loading>
    </div>
</template>

<script>
    import TitleBar from "./titleBar";
    import ApiLoading from "./apiLoading";
    export default {
        name: "redPackageWith",
        components: {ApiLoading, TitleBar},
        data(){
            return {
                loadingShow: false,
                msgList: []
            }
        },
        created() {
            this.$data.loadingShow = true
            this.ser.get("api/v1/mobile/user/withdrawal-red-package").then(res => {
                this.$data.msgList = res.data
                setTimeout(() => {
                    mdui.mutation()
                }, 100)
            }).finally(() => {
                this.$data.loadingShow = false
            })
        },
        methods: {
            howUserQrCode(userId){
                this.$router.push({name: 'userQrCode', params: {userId: userId}})
            },
            showUserInfo(userId){
                this.$data.loadingShow = true
                this.ser.get("api/v1/mobile/user/" + userId + "/info").then(res => {
                    let phone = res.data.phone
                    this.$layer.open({
                        content: '电话:' + res.data.phone,
                        btn: ['取消', '直接拨打'],
                        skin: 'footer',
                        no: (index) => {
                            window.location.href = 'tel:' + phone
                        }
                    })
                }).finally(() => {
                    this.$data.loadingShow = false
                })
            },

            deleteMsg(msgId){
                this.$data.loadingShow = true
                this.ser.delete("api/v1/mobile/user/withdrawal-red-package/" + msgId).then(res => {
                    mdui.snackbar("删除成功")
                }).finally(() => {
                    this.$data.loadingShow = false
                })
            }
        }
    }
</script>

