<template>
	<div>
		<title-bar :title="userInfo.phone + '的收款码'" backBtn="true"></title-bar>
		<div class="content-box-title-bottom">
			<div class="mdui-tab" mdui-tab>
			  <a href="#tab1" class="mdui-ripple">微信</a>
			  <a href="#tab2" class="mdui-ripple">支付宝</a>
			</div>
			<div class="tab-content">
				<div id="tab1" class="mdui-p-a-2">
					<img :src="ser.ctx + userInfo.wechatQrCode"/>
				</div>
				<div id="tab2" class="mdui-p-a-2">
					<img :src="ser.ctx + userInfo.alipayQrCode"/>
				</div>
			</div>
		</div>
		<api-loading :class="{'loading-open': loadingShow}"></api-loading>
	</div>
</template>

<script>
	import titleBar from './titleBar.vue'
	import apiLoading from './apiLoading.vue'
	
	export default {
	  name: 'persionUserQrcode',
	  components:{
		  titleBar,
		  apiLoading,
	  },
	  data(){
		  return {
			  userInfo: {},
			  loadingShow: false
		  }
	  },
	  methods:{
	  },
		created(){
			let userId = this.$route.params.userId
			this.ser.get("api/v1/mobile/user/" + userId + "/info").then(res => {
				this.$data.userInfo = res.data
			}).finally(() => {
				setTimeout(() => {
					mdui.mutation()
				}, 100)
			})
		}
	}
</script>

