<template>
	<div class="animate__animated animate__bounceIn">
		<div class="list-btns">
			<button class="mdui-btn mdui-ripple" @click="gotoRevalue">
				<div><img src="../assets/upMoney.svg"></div>
				升值券
			</button>
			<button class="mdui-btn mdui-ripple" @click="gotoRedMoney">
				<div><img src="../assets/money.svg"></div>
				分红
			</button>
			<button class="mdui-btn mdui-ripple" @click="gotoPage('/persion/make')">
				<div><img src="../assets/make.svg"></div>
				预约
			</button>
			<button class="mdui-btn mdui-ripple" @click="gotoPage('/persion/buys')">
				<div><img src="../assets/store.svg"></div>
				抢购的
			</button>
		</div>
	</div>
</template>

<script>
	export default{
		name:"assetsNav",
		props:{
			userInfo: {}
		},
		methods:{
			gotoPage(path){
				this.$router.push({path:path})
			},
			gotoRevalue(){
				this.$router.push({name:"persionRevalue", query: {
						buyMoney: this.$props.userInfo.buyMoney
				}})
			},
			gotoRedMoney(){
				this.$router.push({name:"persionRedMoney", query: {
					returnMoney: this.$props.userInfo.returnMoney
				}})
			}
		}
	}
</script>

