<template>
	<div class="goods-list-box">
		<div class="mdui-col-xs-6 goods-item animate__animated animate__flipInY" v-for="(goods, index) in goodsList">
			<div class="dumiao">
			  <img src="../assets/orange.svg" /> {{goods.dumiao}}
			</div>
			<div class="mdui-card">
			
			  <!-- 卡片的媒体内容，可以包含图片、视频等媒体内容，以及标题、副标题 -->
			  <div class="mdui-card-media">
			    <img :src="ser.ctx + goods.picture"/>
			  </div>
			  
			  <!-- 卡片的标题和副标题 -->
			  <div class="mdui-card-primary" style="padding-top: 10px; padding-bottom: 0;">
			    <div class="mdui-card-primary-title" v-text="goods.goodsName"></div>
			    <div class="mdui-card-primary-subtitle">价值:<span v-text="goods.priceMin" class="money-text"></span><span class="money-text">~</span><span v-text="goods.priceMax" class="money-text"></span></div>
			  </div>
			  
			  <!-- 卡片的内容 -->
			  <div class="mdui-card-content">
				  <div>抢购: <span v-text="goods.buyTimeStart"></span>~<span v-text="goods.buyTimeEnd"></span></div>
				  <div>预约/抢购: <span v-text="goods.makePrice"></span>/<span v-text="goods.buyPrice"></span></div>
				  <div>合约收益: <span>{{goods.profitsProportion}}%/{{goods.profitsDays}}天</span></div>
			  </div>
			  
			  <!-- 卡片的按钮 -->
			  <div class="mdui-card-actions">
				  <button class="mdui-btn mdui-ripple" v-if="goods.work == 0" style="background-color: #CCC!important;">孕育中</button>
			      <button class="mdui-btn mdui-ripple" v-if="goods.work == 1" @click="makeGoods(goods.id, index)">预约</button>
				  <button class="mdui-btn mdui-ripple" v-if="goods.work == 3" style="background-color: #CCC!important;">已预约</button>
				  <button class="mdui-btn mdui-ripple" v-if="goods.work == 2 || goods.work == 4" style="background-color: #18acbc!important;" @click="buyGoods(goods.id, goods.picture)">抢购</button>
			  </div>
			  
			</div>
		</div>
		<loading-buy :open="showBuy" :closeFun="stopBuy"></loading-buy>
		<api-loading :class="{'loading-open': loadingShow}"></api-loading>
		<buy-result :open="showResult" :closeFun="closeResult" :buyResult="buyResult" :goodsImage="goodsImage" :price="goodsPrice"></buy-result>
	</div>
	
</template>

<script>
	import loadingBuy from './loading-buy.vue'
	import apiLoading from './apiLoading.vue'
	import buyResult from './buyResult.vue'
	
	export default {
		name: 'goodsCard',
		data: function(){
			return {
				goodsList: [],
				loadingShow: false,
				showBuy: false,
				buyCount: 0,
				showResult: false,
				buyResult: false,
				goodsImage: "http://156.96.148.124/comm/display/2020/05/17/eaded2ef80db49e4b8dfba87abfd0e4c",
				goodsPrice: "200"
			}
		},
		created() {
			this.$data.loadingShow = true;
			this.getGoodsList()
			this.timer = setInterval(() => {
				this.getGoodsList()
			},10000)
			this.duMiaoTimer = setInterval(() => {
				this.duMiao()
			},1000)
		},
		methods:{
			closeResult(){
				this.$data.showResult = false
			},
			buyGoods(templateId, goodsImage){
				this.$data.showBuy = true
				this.$data.buyCount = 0;
				this.$data.goodsImage = this.ser.ctx + goodsImage;
				this.$data.goodsPrice = "";
				this.startBuy(templateId, goodsImage)
			},
			startBuy(templateId, goodsImage){
				this.$data.buyCount ++
				if(this.$data.buyCount > 10){
					//达到10次时， 不是自动抢购就自动结束
					if(!localStorage.getItem("authBuy")){
						this.$data.showBuy = false
						this.$data.buyResult = false
						this.$data.showResult = true
						return
					}
				}
				this.ser.post("api/v1/mobile/goods/" + templateId + "/buy").then(res => {
					this.$data.goodsPrice = res.data.responseBody.price + "";
					this.$data.showBuy = false
					this.$data.buyResult = true
					this.$data.showResult = true
				}).catch((res) => {
					if (res.data.message === "您今天已经抢购该资产了，不能重复抢购"){
						this.$data.showBuy = false
					}
					if (res.data.message === '请先进行实名认证'){
						this.$data.showBuy = false
					}
					if(res.data.message.indexOf("当前时间段不能") !== -1){
						this.$data.showBuy = false
						this.$data.buyResult = false
						this.$data.showResult = true
					}
				}).finally(()=>{
					if(this.$data.showBuy){
						let num = this.randomNum(1000, 10000)
						setTimeout(() => {
							this.startBuy(templateId)
						}, num);
					}
				})
				
			},
			randomNum(minNum,maxNum){ 
			    switch(arguments.length){ 
			        case 1: 
			            return parseInt(Math.random()*minNum+1,10); 
			        break; 
			        case 2: 
			            return parseInt(Math.random()*(maxNum-minNum+1)+minNum,10); 
			        break; 
			            default: 
			                return 0; 
			            break; 
			    } 
			},
			stopBuy(){
				this.$data.showBuy = false
			},
			makeGoods(templateId, index){
				this.$data.loadingShow = true;
				this.ser.post("api/v1/mobile/goods/" + templateId + "/make").then(res => {
					this.$data.goodsList[index].work = 3
				}).finally(()=>{
					this.$data.loadingShow = false;
				});
			},
			getGoodsList(){
				this.ser.get("api/v1/mobile/goods").then(res => {
					for (var i = 0; i < res.data.length; i++){
						let goods = res.data[i];
						let startTime = goods.buyTimeStart;
						let endTime = goods.buyTimeEnd;
						let date = new Date();
						startTime = this.dateFmt("yyyy/MM/dd", date) + " " + startTime;
						endTime = this.dateFmt("yyyy/MM/dd", date) + " " + endTime;
						let endDate = new Date(Date.parse(endTime));
						let startDate = new Date(Date.parse(startTime));
						if (new Date().getTime() > endDate.getTime()){
							startDate = new Date(startDate.getTime() + 1000 * 60 * 60 * 24)
							startTime = this.dateFmt("yyyy/MM/dd", startDate) + " " +  goods.buyTimeStart;
						}
						if (new Date().getTime() >= startDate.getTime()){
							// 开始抢了
							goods.dumiao = "抢购时间内"
							let thisWorkVal = goods.work
							if (thisWorkVal == 1 || thisWorkVal == 3 || thisWorkVal == 4){
								thisWorkVal = 4;
							}else{
								thisWorkVal = 2;
							}
							goods.work = thisWorkVal;
						}else{
							let lasTime = this.getDate(startTime);
							goods.dumiao = "距抢购:" + lasTime.hour + ":" + lasTime.minute + ":" + lasTime.second
						}
					}
					this.$data.goodsList = res.data
				}).finally(a => {
					this.$data.loadingShow = false;
				})
			},
			dateFmt(fmt, date){ //author: meizz
				var o = {
					"M+" : date.getMonth()+1,                 //月份
					"d+" : date.getDate(),                    //日
					"h+" : date.getHours(),                   //小时
					"m+" : date.getMinutes(),                 //分
					"s+" : date.getSeconds(),                 //秒
					"q+" : Math.floor((date.getMonth()+3)/3), //季度
					"S"  : date.getMilliseconds()             //毫秒
				};
				if(/(y+)/.test(fmt))
					fmt=fmt.replace(RegExp.$1, (date.getFullYear()+"").substr(4 - RegExp.$1.length));
				for(var k in o)
					if(new RegExp("("+ k +")").test(fmt))
						fmt = fmt.replace(RegExp.$1, (RegExp.$1.length==1) ? (o[k]) : (("00"+ o[k]).substr((""+ o[k]).length)));
				return fmt;
			},
			tow(n) {
				return n >= 0 && n < 10 ? '0' + n : '' + n;
			},
			getDate(willTime) {
				var oDate = new Date();//获取日期对象
				var oldTime = oDate.getTime();//现在距离1970年的毫秒数
				var newDate = new Date(willTime);
				var newTime = newDate.getTime();//2019年距离1970年的毫秒数
				var second = Math.floor((newTime - oldTime) / 1000);//未来时间距离现在的秒数
				var day = Math.floor(second / 86400);//整数部分代表的是天；一天有24*60*60=86400秒 ；
				second = second % 86400;//余数代表剩下的秒数；
				var hour = Math.floor(second / 3600);//整数部分代表小时；
				second %= 3600; //余数代表 剩下的秒数；
				var minute = Math.floor(second / 60);
				second %= 60;
				return {
					day: this.tow(day),
					hour: this.tow(hour),
					minute: this.tow(minute),
					second: this.tow(second)
				};
			},
			duMiao(){
				for (var i = 0; i < this.$data.goodsList.length; i++){
					let goods = this.$data.goodsList[i];
					let startTime = goods.buyTimeStart;
					let endTime = goods.buyTimeEnd;
					let date = new Date();
					startTime = this.dateFmt("yyyy/MM/dd", date) + " " + startTime;
					endTime = this.dateFmt("yyyy/MM/dd", date) + " " + endTime;
					let endDate = new Date(Date.parse(endTime));
					let startDate = new Date(Date.parse(startTime));
					if (new Date().getTime() > endDate.getTime()){
						startDate = new Date(startDate.getTime() + 1000 * 60 * 60 * 24)
						startTime = this.dateFmt("yyyy/MM/dd", startDate) + " " + goods.buyTimeStart;
					}
					if (new Date().getTime() >= startDate.getTime()){
						// 开始抢了
						goods.dumiao = "抢购时间内"
						let thisWorkVal = goods.work
						if (thisWorkVal === 1 || thisWorkVal === 3 || thisWorkVal === 4){
							thisWorkVal = 4;
						}else{
							thisWorkVal = 2;
						}
						goods.work = thisWorkVal;
					}else{
						let lasTime = this.getDate(startTime);
						goods.dumiao = "距抢购:" + lasTime.hour + ":" + lasTime.minute + ":" + lasTime.second
					}
				}
			}
		},
		components:{
			loadingBuy,
			apiLoading,
			buyResult
		},
		beforeDestroy(){
		  clearInterval(this.timer);
		  clearInterval(this.duMiaoTimer);
		},
	}
</script>

<style>
	.mdui-card-content{
		font-size: 10px;
	}
	.mdui-card-media img{
		height: 7rem;
		object-fit: cover;
		margin: auto;
		width: 100%;
	}
	.money-text{
		color: #ff5f01;
	}
	.goods-item{
		padding: 10px;
		margin-top: 20px;
	}
	.mdui-card-actions {
		text-align: center;
	}
	.mdui-card-actions .mdui-btn{
		background-color: #ff9800;
		border-radius: 20px;
		color: white;
	}
	.goods-list-box .mdui-col-xs-6{
		display: inline-block;
		float: none;
	}
	.dumiao{
		border-radius: 10px;
		background-color: #ff9800;
		font-size: 12px;
		padding: 3px;
		position: absolute;
		z-index: 1;
		width: calc(100% - 50px);
		margin-left: 12px;
		margin-top: -15px;
		box-shadow: black 2px 2px 10px -3px;
		color: white;
	}
	.dumiao img{
		height: 25px;
		width: 25px;
		vertical-align:middle
	}
</style>
