<script>
	import titleBar from './titleBar.vue'
	import apiLoading from './apiLoading.vue'
	
	
	export default {
		name: 'persionGroup',
		components:{
		  	titleBar,
			apiLoading
		},
		data(){
			return {
				loadingShow: false,
				subUsers: {
					l1: [],
					l2: [],
					l3: []
				}
			}
		},
		created() {
			this.loadSubPersions()
		},
		methods: {
			loadSubPersions(){
				this.ser.get("api/v1/mobile/user/subs").then(res => {
					this.$data.subUsers = res.data
				}).finally(() => {
					setTimeout(() => {
						mdui.mutation()
					}, 100)
				})
			},
			revalue(userId){
				// 为下级用户充值
				mdui.prompt("充值金额", money => {
					if (!money){
						mdui.snackbar("请输入充值的金额")
						return
					}
					this.$data.loadingShow = true
					this.ser.put("api/v1/mobile/user/" + userId + "/money", {
						money: money
					}).then(res => {
						mdui.snackbar("充值成功")
					}).finally(() => {
						this.$data.loadingShow = false
					})
				}, function(){}, {
					confirmText: "充值",
					cancelText: "取消",
					confirmOnEnter: true
				})

			},
			upStatus(userId){
				mdui.snackbar("尚未开放")
			}
		}
	}
</script>

