<template>
    <div>
        <title-bar title="我的升值券" backBtn="true"></title-bar>
        <div class="content-box-title-bottom">
            <div class="money-block">
                <div>总持有(元)</div>
                <div class="money-revalue" v-text="userInfo.buyMoney">998.99</div>
            </div>
            <div class="text-block">
                说明: 升值券由您的下级用户首次充值时会根据首冲的金额比率获得, 升值券可用于将您的资产价值无需等待直接提升价值. 并且效果可与资产默认升值比率叠加.
                <br/><br/>
                升值券的获得比率每个地区代理或团队长所在的团队会有所不同, 具体可咨询您的上级.
                <br/><br/>
                点击下方资产为资产直接升值.
            </div>

            <ul class="mdui-list">
                <div v-for="item in buyGoodsInfo">
                    <li class="mdui-list-item mdui-ripple" @click="openUseRevalueBox(item)">
                        <div class="mdui-list-item-avatar"><img :src="ser.ctx + item.picture"/></div>
                        <div class="mdui-list-item-content">
                            <div class="mdui-list-item-title" v-text="item.goodsName"></div>
                            <div class="mdui-list-item-text mdui-list-item-two-line">
                                ID: {{item.goodsId}}&nbsp;价值:{{item.price}}
                                <br/>
                                购买时间: {{item.buyDate}}
                            </div>
                        </div>
                    </li>
                    <li class="mdui-divider-inset mdui-m-y-0"></li>
                </div>
            </ul>
        </div>
        <api-loading :class="{'loading-open': loadingShow}"></api-loading>
        <layer v-model="showLayer" @sure="useRevalue" @cancel="closeBox" :btn="['确定使用', '放弃']">
            <div class="mdui-textfield mdui-textfield-floating-label alert-content">
                <label class="mdui-textfield-label">请输入要使用的升值券数目</label>
                <input class="mdui-textfield-input" v-model="useMoneys" type="number"/>
            </div>
        </layer>
    </div>
</template>

<script>
    import titleBar from './titleBar.vue'
    import apiLoading from './apiLoading.vue'

    export default {
        name: "persionRevalue",
        data(){
            return {
                loadingShow: false,
                showLayer: false,
                useMoneys: '',
                buyGoodsInfo: [],
                selectGoods: 0,
                userInfo: {}
            }
        },
        components:{
            titleBar,
            apiLoading,
        },
        methods: {
            closeBox(){
                this.$data.showLayer = false
            },
            openUseRevalueBox(goods){
                this.$data.selectGoods = goods
                this.$data.showLayer = true
            },
            useRevalue(){
                if (!this.$data.useMoneys || this.$data.useMoneys <= 0){
                    this.$layer.msg("请输入升值券数目");
                    return
                }
                this.$data.loadingShow = true
                this.$data.showLayer = false
                this.ser.post("api/v1/mobile/assets/useRevalue", {
                    id: this.$data.selectGoods.goodsId,
                    price: this.$data.useMoneys
                }).then(res => {
                    mdui.snackbar("资产升值成功")
                    this.$data.userInfo.buyMoney -= this.$data.useMoneys
                    this.$data.selectGoods.price = parseFloat(this.$data.selectGoods.price) + parseFloat(this.$data.useMoneys)
                    this.$route.query.buyMoney = this.$data.userInfo.buyMoney
                    this.$data.useMoneys = 0
                }).finally(() => {
                    this.$data.loadingShow = false
                })
            },
            listUsingGoods(){
                this.$data.loadingShow = true
                this.ser.get("api/v1/mobile/goods/buys").then(res => {
                    for(let i in res.data){
                        let item = res.data[i];
                        if(item.status === 1){
                            this.$data.buyGoodsInfo.push(item);
                        }
                    }
                }).finally(() => {
                    this.$data.loadingShow = false
                })
            }
        },
        created() {
            this.$data.userInfo = JSON.parse(localStorage.getItem("user"))
            this.listUsingGoods()
        }
    }
</script>

<style scoped>
    .money-block{
        background-color: #ff9800;
        height: 100px;
        width: 100%;
        color: white;
        padding: 10px;
    }
    .money-block .money-revalue{
        font-size: 30px;
        padding-top: 30px;
    }
    .text-block{
        background-color: white;
        width: calc(100 - 20px);
        margin: 10px;
        box-shadow: rgba(0,0,0,.1) 0 0 15px 0;
        border-radius: 10px;
        padding: 10px;
        line-height: 20px;
        color: gray;
    }
    .alert-content{
        text-align: left;
    }
</style>